import React, { Component } from 'react'

class Summary extends Component {
  constructor() {
    super()
    this.state = {
      feedback: ''
    }

    this.showDetail = this.showDetail.bind(this)
    this.getToken = this.getToken.bind(this)
    this.showOtherDetail = this.showOtherDetail.bind(this)
  }

  showDetail() {
    // Show detail by calling the .showDetail() on the microapp
    // The URL should be another microapp ready webpage, showing
    // standard webpages is not supported at this time

    // In this example I'm just showing summary again
    this.props.app.showDetail({
      url: `${window.location.href.split('#')[0]}#/detail`,
      title: 'iFrame Detail'
    })

    this.setState({
      feedback: 'Show Detail Sent'
    })
  }

  showOtherDetail() {
    // Show detail of another domain (we are showing the vanilla external app)
    this.props.app.showDetail({
      url: `https://workgrid-microapp-1.sandbox.paas.lmig.com`,
      title: 'iFrame Detail'
    })

    this.setState({
      feedback: 'Show Detail Sent'
    })
  }

  getToken() {
    this.setState({
      feedback: 'Asking for Token...'
    })
    setTimeout(async () => {
      const token = await this.props.app.getToken()
      this.setState({
        feedback: `Got Token ${token.length}`
      })
    }, 1000)
  }

  render() {
    const { feedback } = this.state
    return (
      <div className="App">
        <p>MicroApp iFrame Test</p>
        <pre>{feedback}</pre>
        <a href="https://www.bing.com?q=ðŸ¤ª" target="_blank" rel="noopener noreferrer">
          See Something
        </a>
        <br />
        <a href="tel:1-000-000-0000" target="_top">
          Call Someone
        </a>
        <br />
        <input type="text" style={{ width: '80%', padding: 8, margin: 15 }} />
        <div className="ActionBar">
          <button onClick={this.showDetail}>Show Detail</button>
          <button onClick={this.showOtherDetail}>Show Diff Domain Detail</button>
          <button onClick={this.getToken}>Get Token</button>
        </div>
      </div>
    )
  }
}

export default Summary
