import React, { Component } from 'react'

class Detail extends Component {
  constructor() {
    super()
    this.state = {
      url: '',
      feedback: 'Loaded'
    }

    this.hideDetail = this.hideDetail.bind(this)
    this.getToken = this.getToken.bind(this)
  }

  componentDidMount() {
    this.toNavigate = 4

    this.ticker = setInterval(() => {
      this.setState({
        feedback: `Navigating in ${this.toNavigate--}`
      })
    }, 1000)

    setTimeout(() => {
      clearInterval(this.ticker)
      this.toNavigate = 5
      this.setState({
        url: 'https://catapultsgame.com/app/'
      })
    }, 5000)
  }

  hideDetail() {
    this.props.app.hideDetail()
    this.setState({
      feedback: 'Detail hidden'
    })
  }

  getToken() {
    this.setState({
      feedback: 'Asking for Token...'
    })
    setTimeout(async () => {
      const token = await this.props.app.getToken()
      this.setState({
        feedback: `Got Token ${token.length}`
      })
    }, 1000)
  }

  render() {
    const { feedback } = this.state
    return (
      <div className="App">
        <h3>Detail</h3>
        <pre>{feedback}</pre>
        <a href="https://www.google.com?q=ðŸ¤ª" target="_blank" rel="noopener noreferrer">
          See Something
        </a>
        <br />
        <a href="tel:1-715-570-2543" target="_top">
          Call Someone
        </a>
        <br />
        <p>Empty iframe:</p>
        <iframe title="shinanigans" src={this.state.url} />
        <input type="text" style={{ width: '80%', padding: 8, margin: 15 }} />
        <div className="ActionBar">
          <button onClick={this.hideDetail}>Hide Detail</button>
          <button onClick={this.getToken}>Get Token</button>
        </div>
      </div>
    )
  }
}

export default Detail
